<template>
  <v-sheet tile>
    <v-sheet
      class="mt-6"
      tile
      v-if="!this.isLoading"
    >
      <header-title class="mt-7 mb-2">
        {{ title1_text }}
      </header-title>
      <header-small-title class="my-4">
        {{ title2_text }}
      </header-small-title>

      <product-slide-view
        class="mt-2 p-1"
        :class="{
          'ml-0': $vuetify.breakpoint.smAndDown,
          'ml-n4': $vuetify.breakpoint.mdAndUp,
        }"
        :cards="exchangeVariantColorOptions"
        :on-card-selected="onColorSelected"
        :default-value="this.defaultColorValue"
      />
      <header-small-title class="my-6">
        {{ title3_text }}
      </header-small-title>
      <size-dropdown
        :default-value="this.defaultSizeValue"
        :items="exchangeVariantSizeOptions"
        :on-change="onSizeSelected"
      />
      <v-row
        class="mt-4"
      >
        <v-col>
          <action-button
            @click.stop="onClickMethod"
            primary-color
            :loading="isLoading || isMapLoading"
            :block="$vuetify.breakpoint.smAndDown"
            large
            :disabled="isDisabled"
          >
            {{ isDisabled ? action1_Text : action1_Selected_Text }}
          </action-button>
        </v-col>
      </v-row>
    </v-sheet>
    <div v-else>
      <v-skeleton-loader type="card" />
    </div>
  </v-sheet>
</template>

<script>
import { mapGetters } from 'vuex'
import {
  getColorOptionsForPDP,
  saveSelectedItemToStore,
  getSizeOptionsForSelectedVariant,
  handleColorOrSizeOptionSelected,
  getDefaultValue,
  isSameVariant
} from '@/controllers/ExchangesController'
import { CONSTANTS } from '@/constants'

export default {
  name: 'ExchangeOtherView',
  components: {
    ProductSlideView: () =>
      import('@/components/organism/ProductSlideView.vue'),
    ActionButton: () => import('@/components/atoms/ActionButton.vue'),
    SizeDropdown: () => import('@/components/molecules/SizeDropdown.vue'),
    HeaderTitle: () => import('@/components/molecules/HeaderTitle.vue'),
    HeaderSmallTitle: () =>
      import('@/components/molecules/HeaderSmallTitle.vue')
  },
  data: () => ({
    title1_text: "Let's try another size or color",
    title2_text: 'Select a color',
    title3_text: 'Select a size',
    action1_Text: 'Select an item to exchange',
    action1_Selected_Text: 'Exchange for this item',
    action2_Text: 'See all sizes and colors...',
    selectedItem: {},
    exchangeVariantColorOptions: [],
    exchangeVariantSizeOptions: [],
    RETURN_REASON_ACTION: CONSTANTS.RETURN_REASON_ACTION,
    ACTION_TYPE: CONSTANTS.ACTION_TYPE,
    isDisabled: true,
    defaultSizeValue: null,
    defaultColorValue: null
  }),
  props: {
    returnReasonClicked: {
      type: Function,
      required: true
    },
    isMapLoading: {
      type: Boolean,
      required: true
    }
  },
  computed: {
    ...mapGetters({
      isLoading: 'exchange/isLoading',
      currentItemIndex: 'returns/currentItemIndex',
      selectedItems: 'order/selectedItems',
      exchangeList: 'order/exchangeList',
      reason: 'returns/reason'
    }),
    item () {
      return this.selectedItems[this.currentItemIndex]
    }
  },
  methods: {
    onColorSelected (selectedItem) {
      if (!selectedItem.disabled) {
        this.selectedColor = selectedItem.color
        this.handleOptionSelected(selectedItem)
      }
    },
    onSizeSelected (selectedItem) {
      if (!selectedItem.disabled) {
        this.selectedSize = selectedItem.size
        this.handleOptionSelected(selectedItem, this.ACTION_TYPE.SIZE)
      }
    },
    onClickMethod (event) {
      let optionForSelectedSizeAndColor = this.item?.exchangeVariants.find(
        (variant) =>
          variant.size === this.selectedSize &&
          variant.color === this.selectedColor
      )

      if (optionForSelectedSizeAndColor) {
        optionForSelectedSizeAndColor = {
          ...optionForSelectedSizeAndColor,
          disabled: false,
          subtitle: ''
        }
        saveSelectedItemToStore(optionForSelectedSizeAndColor)
        this.returnReasonClicked()
        this.$emit('click', event)
      }
    },
    updateIsDisabled () {
      this.isDisabled = !(
        this.selectedSize &&
        this.selectedColor &&
        this.selectedItem &&
        !isSameVariant(this.selectedItem) &&
        !this.selectedItem.disabled &&
        !this.isLoading
      )
    },
    handleOptionSelected (selectedItem, action = this.ACTION_TYPE.COLOR) {
      const {
        selectedSize,
        selectedColor,
        selectedItem: updatedSelectedItem
      } = handleColorOrSizeOptionSelected(
        selectedItem,
        action,
        this.item,
        this.selectedSize,
        this.selectedColor
      )

      this.selectedSize = selectedSize
      this.selectedColor = selectedColor
      this.selectedItem = updatedSelectedItem

      if (action === this.ACTION_TYPE.COLOR) {
        const defaultValue = getDefaultValue(
          this.ACTION_TYPE.SIZE,
          this.selectedColor
        )
        const sizes = getSizeOptionsForSelectedVariant(this.selectedColor)
        this.exchangeVariantSizeOptions = sizes
        if (!this.selectedSize && sizes.length === 1 && !sizes[0].isDisabled) {
          this.selectedSize = sizes[0].size
          this.defaultSizeValue = sizes[0]
        } else {
          this.selectedSize = defaultValue.size
          this.defaultSizeValue = defaultValue
        }
      }

      this.updateIsDisabled()
    }
  },
  mounted () {
    this.selectedItem = this.item
    const colorOptions = getColorOptionsForPDP()
    this.exchangeVariantColorOptions = colorOptions

    this.exchangeVariantSizeOptions = getSizeOptionsForSelectedVariant(
      this.item?.color
    )

    const defaultValue = getDefaultValue(
      this.ACTION_TYPE.SIZE,
      this.exchangeVariantColorOptions[0].color
    )
    this.defaultSizeValue = defaultValue
    this.defaultColorValue = colorOptions.find(
      (variant) => variant.color === defaultValue?.color
    )
    this.selectedSize = this.defaultSizeValue?.size
    this.selectedColor = this.defaultColorValue?.color
    this.handleOptionSelected(this.selectedItem)
    this.updateIsDisabled()
  }
}
</script>
